* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  overflow: hidden;
  position: relative;
  height: 100vh;
  width: 100vw;
}

/* Header Section */
nav {
  padding: 1rem 2rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-shadow: 1px 3px 10px rgba(150, 150, 150, 1);
  background-color: rgba(25, 25, 25, 1);
}

.nav-brand {
  display: flex;
  align-items: center;
  justify-content: center;
}

.brand-logo {
  width: 4rem;
  margin-right: 0.5rem;
}

.brand-name {
  color: skyblue;
  font-size: 1.5rem;
  font-weight: bold;
}

.nav-item {
  color: white;
  font-size: 1.35rem;
  font-weight: 500;
}

/* Content Section */
.content {
  width: 100%;
  height: 100vh;
  padding: 1rem 2rem;
  color: white;
  background-color: rgba(32, 32, 32, 1);
  background-image: url(../public/logo512.png);
  background-repeat: no-repeat;
  background-position: 110% 100%;
  background-size: 400px;
  background-blend-mode: overlay;
}

h1 {
  font-size: 3rem;
  margin: 3rem 0 4rem 0;
}

ul {
  margin-left: 3rem;
}

.content > ul > li::marker {
  color: deepskyblue;
}

.content > ul > li {
  color: rgba(225, 225, 225, 1);
  list-style-image: deepskyblue;
  font-size: 1.5rem;
  margin: 2rem auto;
  font-weight: 500;
}

/*Footer Section*/
.footer {
  position: absolute;
  color: white;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(25, 25, 25, 1);
  padding: 1rem;
  width: 100%;
}
